.panel {
  min-width: 290px;
  width: 290px;
  max-height: 100%;
  flex-basis: 290px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.panel_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.panel_header_title {
  font-weight: bold;
  font-size: 1rem;
  display: flex;
  gap: 5px;
  align-items: center;
}

.panel_header_title span {
  color: rgb(145, 145, 145);
}

.panel_top_more {
  cursor: pointer;
  position: relative;
}

.panel_dropdown {
  box-shadow: 1px 0px 20px rgba(0, 0, 0, 0.12);
  padding: 10px 20px;
  width: 150px;
  cursor: default;
  background: #fff;
  border-radius: 5px;
}
.panel_dropdown p {
  cursor: pointer;
  margin-bottom: 0;
}

.panel_cards {
  background-color: #f8f8f8;
  padding: 10px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  overflow-y: auto;
}

.panel_add-card {
  background-color: #fff;
  color: #000;
  border-radius: 10px;
  box-shadow: 1px 1px 0 1px rgba(0, 0, 0, 0.12);
  width: 100%;
  text-align: center;
}
.panel_add-card_edit {
  background-color: #fff;
  border-radius: 10px;
  padding: 10px;
}
